<template>
  <div class="OrderDetail">
    <!-- 头部 -->
    <div class="navtop">
      <van-nav-bar title="订单详情" left-arrow @click-left="back" />
      <!-- 订单进度 -->
      <div class="step">
        <van-steps :active="active" active-color="#ff8600">
          <van-step>待付款</van-step>
          <van-step>待发货</van-step>
          <van-step>待收货</van-step>
          <van-step>待评价</van-step>
          <van-step>已完成</van-step>
        </van-steps>
      </div>
    </div>

    <div class="content">
      <!-- 商品列表 -->
      <div class="goods">
        <div class="head">共计1件商品</div>
        <ul class="orderList">
          <Card
            v-for="(item, i) in orderData.cartInfo"
            :key="i"
            :orderObj="item"
          ></Card>
        </ul>
        <div class="relation">
          <van-icon name="service-o" color="#ff8600" size="20" />
          <span>联系卖家</span>
        </div>
      </div>

      <!-- 订单信息 -->
      <div class="orderInfo">
        <h2 class="title">订单信息</h2>
        <van-cell-group>
          <van-cell
            title="收货信息:"
            :value="`${orderData.real_name},${orderData.user_phone},${orderData.user_address}`"
          />
          <van-cell title="订单编号:" :value="orderData.order_id" />
          <van-cell title="下单时间:" :value="orderData._add_time" />
          <van-cell title="支付状态:" :value="status" />
          <van-cell title="支付方式:" :value="payType" />
          <van-cell title="商品价格:" :value="'￥' + orderData.total_price" />
          <van-cell title="快递费用:" :value="'￥' + orderData.total_postage" />
        </van-cell-group>
        <div class="price">
          实付款<span>￥{{ orderData.pay_price }}</span>
        </div>
      </div>
    </div>

    <!-- 提交/取消订单 -->
    <div class="fooder">
      <van-button size="small" plain round color="#ccc" @click="cancelOrder"
        >取消订单</van-button
      >
      <van-button size="small" round color="red" @click="payment">立即付款</van-button>
    </div>
  </div>
</template>

<script>
import "../assets/style/orderDetail.less";
import Card from "../components/GoodsCard";
export default {
  name: "OrderDetail",

  components: {
    Card,
  },

  data() {
    return {
      //订单状态
      active: 0,
      status:"",

      //订单支付方式
      payType:"",

      //订单key
      orderId: "",

      //订单信息
      orderData: [],
    };
  },

  created() {
    //获取订单的key
    this.orderId = this.$route.params.orderId;
    console.log(this.orderId);

    this.getOrderData();
  },

  methods: {
    // 返回上一级
    back() {
      this.$router.push("/order");
    },

    // 获取订单信息
    getOrderData() {
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.axios({
        method: "GET",
        url: this.baseUrl + "/order/detail/" + this.orderId,
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          console.log(res);

          if (res.data.status == 200) {
            this.orderData = res.data.data;
            this.status = this.orderData._status._title
            this.payType = this.orderData._status._payType
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //取消订单
    cancelOrder() {
      this.$dialog
        .confirm({
          title: "提示",
          message: "确定要取消订单吗？",
        })
        .then(() => {
          let tokenString = this.$cookies.get("tokenString");

          if (!tokenString) {
            console.log("浏览器token不存在");
            return this.$router.push({ name: "Login" });
          }

          this.axios({
            method: "POST",
            url: this.baseUrl + "/order/cancel",
            data: {
              id: this.orderId,
            },
            headers: {
              "Authori-zation": "Bearer " + tokenString,
            },
          })
            .then((res) => {
              // console.log(res);

              this.$toast({
                message: res.data.msg,
                forbidClick: true,
                duration: 1000,
              });

              this.$router.push({ name: "Order" });
            })
            .catch((err) => {
              console.log("err ==> ", err);
            });
        })
        .catch(() => {});
    },

    //立即付款
    payment() {
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.axios({
        method: "POST",
        url: this.baseUrl + "/order/pay",
        data: {
          from: "weixinh5",
          paytype: "yue",
          uni: this.orderId,
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          // console.log(res);

          this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 3000,
          });

        })
        .catch((err) => {
          console.log("err ==> ", err);
        });
    },
  },
};
</script>